@import '_mixin';

article {
	font-family: 'Roboto', sans-serif;

	h2 {
		font-family: 'Noto Sans JP', sans-serif;
		color: $darkest-grey;

		> span {
			color: $primary;
		}
	}

	&.p-header {
		h1 {
			font-family: 'Noto Sans JP', sans-serif;
			color: $primary-dark;

			> span {
				color: $primary;
			}
		}

		h4 {
			font-family: 'Roboto', sans-serif;
			color: $darker-grey;
			line-height: 2rem;
		}

		@include brandButton;

		.uk-button-default {
			color: $darker-grey;
		}
	}

	&.p-features {
		h3 {
			color: $primary;
		}

		p {
			line-height: 2rem;
		}

		pre {
			background: $primary-editorBg;
			color: $primary-editor;
			border: 0;
			border-radius: 5px;
		}

		h5 {
			> a {
				&:link, &:visited {
					> span {
						color: $primary;
						@include animation;
						
						&:nth-child(2) {
							border-radius: 50%;
							border: 0.12rem solid $primary;
							width: 35px;
							height: 35px;
							align-items: center;
							justify-content: center;
							display: inline-flex;
							@include animation;
						}
					}
				}

				&:hover, &:active, &:focus {
					text-decoration: none;

					> span {
						color: $primary-dark;

						&:nth-child(2) {
							transform: scale(1.1);
							border-color: $primary-dark;
						}
					}
				}
			}
		}
	}

	&.p-footer {
		> div {
			&:nth-child(1) {
				@include brandButton;
			}
		}

		form {
			> div {
				width: 250px;

				input[type=email] {
					width: 250px;
					padding: 22px;
					border-radius: 5px;
					border: 1px solid $dark-grey;
				}
			}
		}

		button {
			width: 250px;
			padding: 3px;
			border-radius: 5px;
			letter-spacing: 2px;
		}
	}
}